import getNow from './basic/now.js';
/**
 * underscore  waitfunc 
 *
 * @param  {function} func        
 * @param  {number}   wait        
 * @param  {boolean}  immediate   ture
 * @return {function}             
 */
export default function debounce(func, wait, immediate = false) {
	let timeout, args, context, timestamp, result;
 
 	function run() {
		// 
		let last = getNow() - timestamp;
		// 0
		if (last < wait && last >= 0) {
			timeout = setTimeout(run, wait - last);
		} else {
			// 
			timeout = null;
			if (!immediate) {
				result = func.apply(context, args);
				context = args = null;
			}
		}
	};
 
	function debounce() {
		context = this;
		args = arguments;
		// 
		timestamp = getNow();
		// 
		let callNow = immediate && !timeout;
		// 
		if (!timeout) {
			timeout = setTimeout(run, wait);
		}
		if (callNow) {
			//   apply 
			result = func.apply(context, args);
			context = args = null;
		}
		return result;
	};

	return debounce;
};